* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  color: #424769;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.app {
  display: flex;
  flex-direction: column;
  padding-top: 4vh;
  align-items: center;
  justify-content: start;
  gap: 2em;
  background-color: rgba(0, 255, 255, 0.389);
  border-radius: 20px;
  width: 25%;
  padding: 2em;
  box-shadow: 0px 0px 2px 2px black;
  scale: .8;
  transition: all .2s ease-in-out;

  background: rgb(81, 32, 186);
  background: linear-gradient(40deg, rgba(81, 32, 186, 0.803) 1%, rgba(29, 253, 247, 1) 60%);


}

.app:hover {
  box-shadow: 0px 2px 14px 4px black;
  scale: .9;
}

.merriweather-bold {
  font-family: "Merriweather", serif;
  font-weight: 700;
  font-style: normal;
}

input {
  display: flex;
  border-radius: 2em;
  font-size: 1vw;
  padding: 1vh 2vh;
  text-align: center;
  font-weight: 700;
  font-size: 1.1vw;
  width: 20em;
  height: 2.7em;
  border: none;
  transition: all .2s ease-in-out;
  position: relative;
  color: none !important;
  text-shadow: none;
  top: 1em;
}

input:hover {
  box-shadow: 2px 2px 14px 2px black;
  width: 21em;
  height: 3em;
}

input::placeholder {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}

.wht-img>img {
  width: 10vw;
  aspect-ratio: 1;
  position: relative;
  top: 1.5em;
  filter: drop-shadow(5px 3px 2px);
  transition: all ease-in-out .2s;
}

.wht-img>img:hover {
  top: -.1em;
  scale: 1.3;
  filter: drop-shadow(5px 15px 12px);

}

.app>div:nth-child(3) {
  font-size: 2.5vw;

}


.app>div:nth-child(1) {
  position: relative;
}

.locationIcon {
  width: 2em;
  top: 1.3em;
  left: 1em;
  z-index: 1;
  aspect-ratio: 1;
  position: absolute;
}

.temp {
  transition: ease-in-out all .2s;

}

.temp:hover {
  font-size: 3vw;
}

.wthrImg {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-position: center;

}

.app>div:nth-child(4) {
  font-size: 1.8vw;
  position: relative;
  top: -1em;
  transition: ease-in-out .2s all;
}

.app>div:nth-child(4):hover {
  font-size: 2vw;
}

.app>div:nth-child(5) {

  font-size: 1vw;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1em 4em;
  width: 24rem;

  gap: 2em;

  &>div:nth-child(1) {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: .5em;
    transition: all ease-in-out .2s;
  }

  .app>div:nth-child(5)>div:nth-child(1) {
    top: -.1em;
    scale: 1.3;
  }

  &>div:nth-child(2) {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: .5em;
    transition: all ease-in-out .2s;
  }
}

.humibox:hover {
  scale: 1.2;
}

.windbox:hover {
  scale: 1.2;
}

.humiIcon,
.windIcon {
  width: 3em;
  aspect-ratio: 1;
}

.app>div:nth-child(5) p {

  color: #D4FAFC;
}

.tempText {
  font-size: 1em;
  font-weight: 700;
}

@media (width<=430px) {

  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  .app {
    width: 100vh;
    height: fit-content;
    gap: 2em;
    align-items: center;
  }


  .app:hover {
    scale: .8;
    position: relative;
    top: -2em;
  }

  .wht-img>img {
    width: 10em;
    aspect-ratio: 1;
  }

  .temp {
    font-size: 4em;
  }


  .temp:hover {
    font-size: 5em;
  }

  .app>div:nth-child(4):hover {
    font-size: 2.2em;
  }


  input {
    width: 80vw;
    height: 2em !important;
    font-size: 2em;
    position: relative;

  }

  input:hover {
    box-shadow: 2px 2px 14px 2px black;
    width: 80vw;
    height: 2em;

  }

  .locationIcon {
    position: absolute;
    top: 3em;

  }

  .app>div:nth-child(4) {
    font-size: 2em;

  }

  .app>div:nth-child(5) {
    font-size: 1em;
    gap: 2.2em;
    width: 20em;
  }



  .humibox div:nth-child(2) {
    font-size: 1.4em;
  }

  .windbox div:nth-child(2) {
    font-size: 1.4em;

  }



}